<template>
  <div class="time flex-row">
    <m-input hintText="小时" number v-model="hour" @input="hourHandler" :read="read"></m-input><span v-if="option.minute">：</span>
    <m-input hintText="分" number v-model="minute" @input="minuteHandler" v-if="option.minute" :read="read"></m-input><span v-if="option.second">：</span>
    <m-input hintText="秒" number v-model="second" @input="secondHandler" v-if="option.second" :read="read"></m-input>
  </div>
</template>

<script>
export default {
  name: 'PTime',
  props: {
    read: {type: Boolean, default: false},
    option: Object,
    answers: Object
  },
  data () {
    return {
      hour: '',
      minute: '',
      second: ''
    }
  },
  mounted () {
    if (this.answers) {
      this.hour = this.answers.hour
      this.minute = this.answers.minute
      this.second = this.answers.second
    }
  },
  methods: {
    hourHandler () {
      this.handleChange()
    },
    minuteHandler () {
      this.handleChange()
    },
    secondHandler () {
      this.handleChange()
    },
    handleChange () {
      this.hour = this.handleValue(this.hour)
      this.minute = this.handleValue(this.minute)
      this.second = this.handleValue(this.second)
      this.$emit('input', this.hour + ':' + this.minute + ':' + this.second)
      this.$emit('update:answers', {hour: this.hour, minute: this.minute, second: this.second})
    },
    handleValue (val) {
      if (!val) return '0'
      else return val
    }
  }
}
</script>




