<template>
  <div class="address">
    <m-select v-model="myprovince" maxOptionHeight="300px" :read="read">
      <m-option v-for="item, index in address" :key="index" :value="item.province">{{ item.province }}</m-option>
    </m-select>

    <m-select v-model="mycity" maxOptionHeight="300px" :item="choose_city" v-if="cityState" :read="read">
      <m-option v-for="c, cid in choose_city" :key="cid" :value="c">{{ c }}</m-option>
    </m-select>


    <m-input hintText="详细地址" full v-model="mytext" @input="handleInput(arguments[0])" :read="read"></m-input>
  </div>
</template>

<script>
// import $ from 'webpack-zepto'
export default {
  name: 'PAddress',
  props: {
    read: {type: Boolean, default: false},
    option: Object,
    answers: Object
  },
  data () {
    return {
      address: [{province: '北京', city: ['海淀', '99']}, {province: '上海', city: ['hu', 'fashang']}],
      provinceState: true,
      cityState: true,
      myprovince: '',
      mycity: '',
      mytext: ''
    }
  },
  computed: {
    choose_city: function () {
      let mycity = ['']
      this.address.map(city => {
        if (this.myprovince === city.province) {
          mycity = city.city
        }
      })
      return mycity
    }
  },
  mounted () {
    if (this.option) {
      this.provinceState = this.option.province
      this.cityState = this.option.city
    }
    if (this.answers) {
      this.myprovince = this.answers.province
      this.mycity = this.answers.city
      this.mytext = this.answers.text
    }
  },
  watch: {
    myprovince () {
      this.handleChange()
    },
    mycity () {
      this.handleChange()
    },
    answers () {
      this.myprovince = this.answers.province
      this.mycity = this.answers.city
      this.mytext = this.answers.text
    }
  },
  methods: {
    handleInput (val) {
      this.handleChange()
    },
    handleChange () {
      this.myprovince = this.handleValue(this.myprovince)
      this.mycity = this.handleValue(this.mycity)
      this.mytext = this.handleValue(this.mytext)
      if (this.mytext) this.mytext = this.mytext.replace(/\s/g, '')
      this.$emit('input', this.myprovince + ' ' + this.mycity + ' ' + this.mytext)
      this.$emit('update:answers', {province: this.myprovince, city: this.mycity, text: this.mytext})
    },
    handleValue (val) {
      if (!val) return ''
      else return val
    }
  }
}
</script>




