import Vue from 'vue'
import VueRouter from 'vue-router'
/** all link */
import App from './App'
import Login from './views/Login'
import Home from './views/Home'
import Notification from './views/Notification.vue'
import Project from './views/Project.vue'
import Record from './views/Record.vue'
import WorkData from './views/Workdata.vue'
import SaveList from './views/saveList.vue'
import CommitList from './views/CommitList.vue'
import BackList from './views/BackList.vue'
import Search from './views/Search.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  routes: [
    {
      path: '/',
      component: App,
      redirect: {name: 'home'}, //  重定向
      meta: {requiresAuth: true, title: '首页'}
    },
    {
      name: 'home',
      path: '/home',
      component: Home,
      meta: {requiresAuth: true, title: '首页'}
    },
    {
      path: '/notification',
      component: Notification,
      meta: {requiresAuth: true, title: '通知'}
    },
    {
      path: '/project/:projectid',
      component: Project,
      meta: {requiresAuth: true, title: '项目'},
      children: [
        {
          name: 'newrecord',
          path: 'patient/new',
          component: Record,
          meta: {requiresAuth: true, title: '创建新患者病历'}
        },
        {
          name: 'record',
          path: 'patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '编辑患者病历' }
        },
        {
          name: 'workdata-record',
          path: 'workdata/patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '日志-编辑患者病历' }
        },
        {
          name: 'savelist-record',
          path: 'savelist/patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '保存-编辑患者病历' }
        },
        {
          name: 'commitlist-record',
          path: 'commitlist/patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '提交-查看患者病历' }
        },
        {
          name: 'backlist-record',
          path: 'backlist/patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '退回-编辑患者病历' }
        },
        {
          name: 'workdata',
          path: 'workdata',
          component: WorkData,
          meta: {requiresAuth: true, title: '我的工作日志'}
        },
        {
          name: 'savelist',
          path: 'savelist',
          component: SaveList,
          meta: {requiresAuth: true, title: '已保存的病历'}
        },
        {
          path: 'commitlist',
          component: CommitList,
          meta: {requiresAuth: true, title: '已提交的病历'}
        },
        {
          path: 'backlist',
          component: BackList,
          meta: {requiresAuth: true, title: '被退回的病历'}
        },
        {
          name: 'search',
          path: 'search',
          component: Search,
          meta: {requiresAuth: true, title: '搜索病历'}
        }
      ]
    },
    { path: '/login', component: Login },
    { path: '*', redirect: '/home' }
  ]
})

export default router



