<template>
<div id="search-state">
  <div class="search-content">
		<span>搜索：{{ searchText }}</span>
	</div>
	<div class="table">
		<div class="table-header flex-row">
			<span class="w14">患者姓名</span>
			<span class="w10">性别</span>
			<span class="w10">年龄</span>
			<span class="w18">电话</span>
			<span class="w16">ID</span>
			<span class="w14">状态</span>
			<span class="w18">上次更新时间</span>
		</div>
    <up-refresh :elheight="tableHeight - 30" @isdown="getCaseList">
      <div class="table-body">
        <div class="table-tr flex-row" v-for="item in caseList">
          <span class="w14"><router-link :to="{name: 'record', params: {mdid: item.mdid}, query: {redirct: 'search'}}">{{item.u_patientname}}</router-link></span>
          <span class="w10">{{item.u_gender === '1' ? '男' : item.u_gender === '2' ? '女' : ''}}</span>
          <span class="w10">{{ item.u_birthday | dateToAge }}</span>
          <span class="w18">{{item.u_phone}}</span>
          <span class="w16">{{item.mdid}}</span>
          <span class="w14">{{item.status === '1' ? '保存' : item.status === '2' ? '提交（待审核）' : item.status === '3' ? '被退回' : item.status === '4' ? '审核通过' : ''}}</span>
          <span class="w18">{{item.updatetime | formatDate}}</span>
        </div>
        <div class="more" v-if="!empty">{{ moreText }}</div>
        <div class="empty" v-if="empty">
          <img src="../assets/empty/search_empty.svg" alt="medicayun">
        </div> 
      </div>
    </up-refresh>
	</div>
</div>
</template>

<script>
export default {
  name: 'Search',
  props: {
    projectid: String,
    searchText: String
  },
  data () {
    return {
      empty: true,
      caseList: [],
      pagenum: 1,
      totalPage: 0,
      tableHeight: 0,
      moreText: ''
    }
  },
  watch: {
    searchText (val) {
      if (val) {
        this.moreText = '搜索中...'
        this.pagenum = 1
        this.caseList = []
        this.getCaseList()
        this.empty = false
      } else {
        this.empty = true
        this.caseList = []
      }
    }
  },
  mounted () {
    if (this.search === '') this.getCaseList()
    const tableTop = this.$el.getElementsByClassName('table-body')[0].getBoundingClientRect().top
    this.tableHeight = window.innerHeight - tableTop
  },
  methods: {
    getCaseList () {
      this.$http.SearchCase({userid: this.$root.userid, projectid: this.projectid, pagenum: this.pagenum, search: this.searchText}).then(rep => {
        if (this.pagenum === 1 && !rep.data.length) {
          this.empty = true
          return
        } else {
          this.empty = false
        }
        rep.data.map(item => this.caseList.push(item))
        this.totalPage = Math.ceil(rep.totalnum / 30)
        if (rep.data.length > 0) this.pagenum ++
        this.moreText = this.totalPage > this.pagenum - 1 ? '下拉加载更多的记录...' : '没有更多的记录'
      }).catch(err => console.log(err))
    }
  }
}
</script>
<style scoped>
  .search-content {
    line-height: 56px;
    padding-left: 32px;
    color: rgba(0,0,0,.87);
    font-size: 16px;
  }
</style>



